@import '../theme/color.scss';
@import '../theme/style.scss';

.navbar-bg-header {
  background-color: white;
  width: 100%;
  position: fixed;
  padding-top: env(safe-area-inset-top);

  .zp-navbar-wrapper {
    height: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;

    .navbar-title {
      font-size: 1.2rem;
      font-weight: bold;
      line-height: 1.25;
      text-align: center;
      color: #1b1d20 !important;
      flex: 1;
    }

    .navbar-button {
      border: none;
      background-color: transparent;
      margin: 0 10px 0 10px;
      min-width: 50px;
      font-size: 1rem;

      &:active {
        opacity: 0.5;
        border: none;
      }

      &:focus {
        outline: 0;
      }
    }
  }


}

.navbar-wrapper {
  margin-bottom: 88px;
}


.amount-title {
  @include amount-text;
}

.order-item {
  display: flex;
  justify-content: space-between;
  padding: 6px 0px 6px 0px;
  @include normal-text;

  >.order-item-title {
    text-align: left
  }

  >.order-item-description {
    text-align: right
  }
}

.source-money {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px 10px 10px 10px;
  min-height: 48px;

  .zalopay-icon {
    width: 24px;
    height: 24px;
  }

  >.body {
    display: flex;
    margin-left: 15px;
    flex: 1;
  }

  >.source-name {
    flex: 1;
    font-size: 17px;
    margin: 0px 10px 0px 10px;
  }

  >.amount {
    font-family: SFUIText;
    font-size: 17px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: -0.18px;
    text-align: right;
    color: #727f8c;

    >.vnd {
      font-size: 13px;
      font-weight: normal;
    }
  }


}


.navbar-close-button {
  height: 20px;
  color: white;
  align-self: center;
}

.detail {
  padding-top: 70px
}

.item-list {
  align-self: stretch;
  padding: 6px 0px 6px 0px;
  border-top: 1px solid $very-light-pink;
  // border-width: 1px;
  // border-color: $very-light-pink;
  // background-image: //   linear-gradient(to left, $very-light-pink 100%, rgba(255, 255, 255, 0) 0%),
  //   linear-gradient(black 33%, rgba(255, 255, 255, 0) 0%),
