@import './font.scss';
@import './color.scss';
@import './style.scss';

html,
body {
  @include no-selected;
  color: $text-color;
  font-family: "SFUIDisplay";
  font-size: 14px;

  @media (min-width: 360px) {
    font-size: 16px;
  }

  @media (min-width: 480) {
    font-size: 18px;
  }
}

.page {
  display: flex;
  flex-direction: column;

  .body {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
  }
}

p {
  margin: 0 0 0 0;
}

button {
  @include basic-button;
}

.btn {
  @include basic-button;
}

.btn:focus,
.btn:active {
  outline: none !important;
  box-shadow: none !important;
}

footer {
  width: 100%;
  position: fixed;
  bottom: 0px;
  padding-bottom: calc(env(safe-area-inset-bottom));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.normal-button {
  @include title-button;
  border-radius: 4px;
  width: 100%;
  color: white;
  background-color: $main-color;
}

.pay-button {
  @include title-button;
  border-radius: 4px;
  width: 100%;
  color: white;
  background-color: $pay-color;
}

.title {
  font-size: 1.2rem;
  font-weight: bold;
  line-height: 1.25;
  text-align: center;
}

.error {
  color: $error-color;
  text-align: center;
}