@import '../../theme/color.scss';
@import '../../theme/style.scss';


.status-image {
  width: 37.5%;
  max-width: 200px;
  margin-top: 35px;
}

.result-page {
  align-items: center;

  .status-title {
    margin: 16px 0px 12px 0px;
  }

  >.description {
    margin: 12px 10px 15px 10px;
  }

  .footer {
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    background-color: $off-white;
    padding: 10px 10px env(safe-area-inset-bottom) 10px;

    .close-button {
      @include title;
      color: white;
      width: 100%;
      border-radius: 4px;
      background-color: $vibrant-green;
      min-height: 40px;
    }

    >.description {
      margin: 10px 10px 10px 10px;
    }
  }
}