@import '../../theme/color.scss';

.source-money {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px 10px 10px 10px;
  min-height: 48px;

  .zalopay-icon {
    width: 24px;
    height: 24px;
  }

  >.body {
    display: flex;
    margin-left: 15px;
    flex: 1;
  }

  >.source-name {
    flex: 1;
    font-size: 17px;
    margin: 0px 10px 0px 10px;
  }

  >.amount {
    font-family: SFUIText;
    font-size: 17px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: -0.18px;
    text-align: right;
    color: #727f8c;

    >.vnd {
      font-size: 13px;
      font-weight: normal;
    }
  }
}

$small-radio-size: 5px;

$radio-size: $small-radio-size * 4;

.radio-button-wrapper {
  width: $radio-size;
  height: $radio-size;
  background: $bright-blue;
}

.pmc-item-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px 10px 10px 10px;
  min-height: 48px;

  .pmc-name {
    flex: 1;
    font-size: 17px;
    margin: 0px 10px 0px 10px;
  }
}