@import '../../theme/color.scss';


$small-radio-size: 5px;

$radio-size: $small-radio-size * 4;

[type="radio"]:checked,
[type="radio"]:not(:checked) {
  position: absolute;
  left: -9999px;
}



[type="radio"]:checked+label,
[type="radio"]:not(:checked)+label {
  position: relative;
  cursor: pointer;
  display: inline-block;
  background-color: transparent;
  width: $radio-size;
  height: $radio-size;
  margin: 0px 10px 0px 0px;
}

[type="radio"]:checked+label:before,
[type="radio"]:not(:checked)+label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: $radio-size;
  height: $radio-size;
  border: $small-radio-size/2 solid $bright-blue;
  border-radius: 100%;
  background: #fff;
}

[type="radio"]:checked+label:after,
[type="radio"]:not(:checked)+label:after {
  content: '';
  background: $bright-blue;
  position: absolute;
  width: $small-radio-size * 2;
  height: $small-radio-size * 2;
  top: $small-radio-size;
  left: $small-radio-size;
  border-radius: 100%;
  -webkit-transition: all 0.1s ease;
  transition: all 0.1s ease;
}

[type="radio"]:not(:checked)+label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0);
}

[type="radio"]:checked+label:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}