@import './color.scss';


@mixin no-selected {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

@mixin basic-text {
  white-space: pre-line;
  line-height: 1.2;
  font-size: 1rem;
  text-align: center;
  color: $text-color;
}

@mixin amount-text {
  @include basic-text;
  font-size: 1.5rem;
  font-weight: bold;
  line-height: 1.2;
  text-align: center;
  color: $bright-blue;
}

@mixin small-text {
  @include basic-text;
  font-size: 0.9rem;
  line-height: 1.31;
  text-align: center;
  color: $dark-grey
}

@mixin title {
  @include basic-text;
  font-size: 1.2rem;
  font-weight: bold;
  line-height: 1.25;
  text-align: center;
  color: $dark-grey
}

@mixin normal-text {
  @include basic-text;
}

@mixin small-highlight-text {
  @include basic-text;
  font-size: 0.9rem;
  line-height: 1.31;
  text-align: center;
  color: $bright-blue;
}

.small-highlight-text {
  @include small-highlight-text;
}

/* 
big
medium
small
*/

.bold-medium-text {
  @include basic-text;
  font-weight: bold;
}

.bold-big-text {
  @include basic-text;
  font-weight: bold;
  font-size: 1.2rem;
}

.medium-text {
  @include basic-text;
}


@mixin basic-button {
  box-shadow: none !important;
  outline: none !important;
  border: none;
  min-height: 40px;
  min-width: 40px;


  &:active {
    opacity: 0.5;
  }
}

@mixin title-button {
  font-size: 1rem;
  font-weight: bold;
}

.small-description-text {
  @include normal-text;
  color: $slate-grey;
}

.description-text {
  @include normal-text;
  color: $slate-grey;
}