@import '../../theme/style.scss';
@import '../../theme/color.scss';

@mixin line-mixin {
  content: '';
  position: absolute;
  border-left: 2px solid white;
}

.forgot-password {
  .body {
    padding: 10px 10px 60px 10px;
    background-color: white;

    .box {
      border-radius: 4px;
      box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
      background-color: #ffffff;
      margin: 10px 10px 10px 10px;
      padding: 10px;
      width: 100%;
      display: block;
      position: relative;
    }

    >.icon {
      width: 37%;
      max-width: 160px;
      margin: 26px 0 12px 0;
    }

    >.title {
      margin: 12px 0px 16px 0px;
    }

    >.sub-title {
      margin: 24px 0px 12px 0px;
    }
  }

  footer {
    background-color: white;
    padding: 10px 10px calc(calc(env(safe-area-inset-bottom)) + 10px) 10px !important;

    .close-button {
      width: 100%;
      padding: 10px 10px 10px 10px !important;
      border: none;
    }
  }
}

.circle-last {
  &:before {
    content: '';
    top: auto !important;
    bottom: auto !important;
  }
}

.point-wrapper {
  display: flex;
  flex-direction: row;

  .left {
    .circle {
      font-size: 14px;
      line-height: 1.4;
      font-weight: bold;
      color: #fdfdfd;
      background-color: $bright-blue;
      width: 24px;
      height: 24px;
      text-align: center;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      margin: 0px 10px 0px 0px;
      z-index: 1;

      &:before {
        content: '';
        position: absolute;
        border-left: 1px solid $bright-blue;
        left: 12px;
        top: 20px;
        bottom: 0px;
      }
    }
  }

  .right {
    flex: 1;
    display: block;
  }

  padding-bottom: 24px;
  position: relative;
}



.downloads {
  display: flex;
  margin: 10px 0 0 0px;

  a {
    margin-right: auto;

    img {
      width: 120px;
      height: 36px;

    }
  }
}