@import '../../theme/color.scss';
@import '../../theme/style.scss';

.detail-wrapper {
  margin-top: calc(env(safe-area-inset-top) + 50px);

  .body {
    padding: 10px 10px 10px 10px;
  }

  .order-header {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 36px 20px 20px 20px;
    // border-bottom: solid 1px $very-light-pink;

    >.description {
      @include normal-text;
      color: $slate-grey;
      margin-top: 16px;
    }
  }

  .footer-comfirm-button {
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    background-color: $off-white;
    padding: 10px 10px env(safe-area-inset-bottom) 10px;

    .authentication-title {
      color: $slate-grey;
      margin: 10px 0 10px 0;
      @include small-text;
    }

    .confirm-button {
      @include title;
      color: white;
      width: 100%;
      border-radius: 4px;
      background-color: $vibrant-green;
      min-height: 40px;
    }


  }

  .input-code-background {
    position: absolute;
    background-color: #10101080;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    // z-index: 10;
    // footer {
    //   background-color: white;
    //   background-color: white;
    //   justify-content: center;
    //   align-items: center;
    //   display: flex;
    //   flex-direction: column;
    //   border-radius: 4px 4px 0px 0px;
    // }

    .confirm-input-code-title {
      @include normal-text;
      text-align: center;
      margin: 8px 0px 24px 0px;
    }

    .forgot-password {
      @include normal-text;
      color: $bright-blue;
      text-align: center;
      margin: 45px 0px 24px 0px;
    }

    .close-button {
      background-color: transparent;
    }
  }

  p {
    margin: 0px 0px 0px 0px;
  }

  >.text-input {
    // background-color: transparent;
    // border: none;
    // position: absolute;
    // color: transparent !important;
    // caret-color: transparent !important;
    // transform: scale(0.0001) translateX(-100px);
  }
}